@import '_colors';
@import '_reset';
@import '_mixins';

html, body {
  max-width: 100%;
  font-family: 'Roboto', sans-serif;
  div, span, p, h1, h2 {
    letter-spacing: 0.035em;
    font-size: 20px; }
  h1 {
    font-weight: 600;
    text-transform: uppercase; } }

.upper {
  position: fixed;
  z-index: 2;
  width: 100vw;
  height: 60px;
  font-size: 20px;
  .menu_heading {
    display: inline-block;
    width: 100%;
    text-align: center;
    min-height: 60px;
    background: rgba(0,0,0,.3);
    cursor: pointer;
    span {
      color: $white;
      font-size: 1em;
      display: inline-block;
      margin: 20px 0;
      font-weight: 200;
      transition: color ease .2s;
      font-size: 20px;
      &:hover {
        color: $lightblue; } }
    i {
      transition: transform ease .3s; }
    &.active {
      border-radius: 0;
      i {
        transform: rotate(180deg); }
      & + .menu_content {
        max-height: 380px; } } }
  .menu_content {
    display: inline-block;
    max-height: 0;
    overflow-y: hidden;
    transition: max-height ease .5s;
    background: rgba(0,0,0,.3);
    border-radius: 0 0 5px 5px;
    a {
      position: relative;
      display: inline-block;
      width: 100%;
      background: rgba(0,0,0,.6);
      padding: 15px 30px 10px;
      z-index: 1;
      color: $white;
      font-weight: 300;
      cursor: pointer;
      transition: background-color ease .2s;
      font-size: 20px;
      &:last-child {
        border-radius: 0 0 5px 5px; }
      &:hover, &:active {
        text-decoration: none;
        background-color: lighten($lightblue, 8%); } } } }

footer {
  background: $blue;
  padding-top: 25px;
  .row {
    background: $blue; }
  .footer_logo {
    img {
      width: 70%;
      margin: 40px 15%; } }
  .footer_social {
    margin-bottom: 20px;
    text-align: center;
    color: $white;
    p {
      display: flex;
      flex-direction: row;
      align-items: center;
      margin: 10px 0;
      justify-content: center;
      a {
        color: $white; } } } }


section.documents {
  text-align: center;
  font-weight: 300;
  background: $white;
  h1 {
    text-align: center;
    text-transform: none;
    font-weight: 300;
    font-size: 2em;
    margin: 30px 0; }
  .doc_item {
    display: inline-block;
    width: 100%;
    max-width: 270px;
    height: initial;
    margin-bottom: 30px;
    padding: 15px 15px 5px;
    border: 2px solid $blue;
    transition: border-color ease .3s;
    h3 {
      font-size: .9em;
      height: 1.8em;
      color: $blue;
      transition: color ease .3s;
      a:link, a:visited {
        color: $blue; } }
    hr {
      margin: 7px; }
    > a:link, a:visited {
      display: inline-block;
      width: 100%;
      color: $white;
      background: $blue;
      padding: 8px 0;
      font-size: .9em;
      text-transform: uppercase;
      border-radius: 2px;
      font-weight: 400;
      text-decoration: none;
      transition: background ease .3s;
      cursor: pointer; }
    > a:hover, a:active {
      text-decoration: none; }
    p {
      color: $brown;
      text-align: left;
      font-size: .7em;
      line-height: 1.25em;
      height: 210px;
      span {
        border-top: 1px solid #eee;
        margin-top: 15px;
        padding-top: 10px;
        display: inline-block;
        text-align: center;
        a:link, a:visited {
          font-size: .85em;
          background: transparent;
          transition: background ease .3s;
          cursor: pointer;
          color: $blue; }
        a:hover, a:active {
          background: transparent; } } }

    &:hover {
      border-color: $lightblue;
      h3 {
        a {
            color: $lightblue; } }
      > a:link, a:visited {
        background: $lightblue; }
      p {
        span {
          a:link, a:visited {
            color: $lightblue; } } } } } }


@media all and (min-width: 768px) {
  footer {
    .footer_logo {
      img {
        margin-top: 45px; } }
    .footer_social {
      img {
        width: 35px; } } }
  .upper {
    width: 30%;
    min-width: 300px;
    right: 10%;
    float: right;
    .menu_heading {
      border-radius: 0 0 5px 5px; } } }
